import { Component, OnInit } from "@angular/core";
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: "app-footer",
  templateUrl: "./footer.component.html"
})
export class FooterComponent implements OnInit {
  test: Date = new Date();
  clicked = false;
  constructor(private toastr: ToastrService) {}

  ngOnInit() {}

  showNotification(from,align){
    if(this.clicked === true)
    return;
    this.clicked = true;
    this.toastr.info('<span class="tim-icons icon-lock-circle" [data-notify]="icon"></span>&copy; Copyright Mert Kabadayı<br>İletisim: mertkabadayii@hotmail.com<br>Veriler spk.gov adresinden sağlanmaktadır.', '', {
      disableTimeOut: true,
      closeButton: true,
      enableHtml: true,
      toastClass: "alert alert-default alert-with-icon",
      positionClass: 'toast-' + from + '-' +  align
    });
  }
}
