import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from 'rxjs';
import { User } from '../models/user';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})

export class userService {

  private baseUrl = 'http://foncanav-test.eu-central-1.elasticbeanstalk.com/user';

  private constructor(private http: HttpClient) { }

  favorilereEkle(kod: string, username: string): Observable<any> {
    return this.http.put(this.baseUrl + '/favekle?kod=' + kod, {
      username: username
    }, httpOptions);
  }

  favorilerdenCikar(kod: string, username: string): Observable<any> {
    return this.http.put(this.baseUrl + '/favcikar?kod=' + kod, {
      username: username
    }, httpOptions);
  }
  
  sifredegistir(user: User): Observable<any> {
    return this.http.put(this.baseUrl+ '/sifre?key='+ user.pass2, {
      username: user.username,
      pass: user.pass,
    });
  }



}